.overlay {
  height: 100%;
  width: 0%;
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  background-color: rgb(0,0,0);
  background-color: rgba(0,0,0, 0.9);
  overflow-x: hidden;
  transition: 0.5s;
}

.overlay-content {
  position: relative;
  top: 25%;
  width: 100%;
  text-align: center;
  margin-top: 30px;
}

.overlay h2 {
  padding: 8px;
  text-decoration: none;
  font-size: 36px;
  color: #818181;
  display: block;
}

.overlay h2:hover, .overlay h2:focus {
  color: #f1f1f1;
}

.overlay .closebtn {
  position: absolute;
  background: url(../resources/x-sign-x-png-black-and-white-200_200.png) no-repeat;
  background-size: 80px;
  width: 80px;
  height: 80px;
  top: 20px;
  right: 45px;
}